import React from 'react';
import PropTypes from "prop-types";
function Players({ name, cost, category }) {
    return (
        <>

            <div className="card" style={{ backgroundColor: "lightGray" }}>
                
                <a href={"https://www.google.com/search?q="+ name}>

                <h3 className="card-title" style = {{backgroundColor:"gray" , color:"wheat", cursor : "pointer"}}> ✨{name}</h3>
                </a>


                <div className="card-text">
                    <ul style={{ listStyle: "square" }}>

                        <li style={{ fontSize: "15px" }}>Fiyat: <b>{cost}</b> </li>
                        <li style={{ fontSize: "15px" }}>Kategori: <b>{category}</b></li>
                    </ul>
                </div>
            </div>

        </>
    )
}

Players.propTypes = {
    name: PropTypes.string.isRequired,
    cost: PropTypes.string.isRequired,
    category: PropTypes.string.isRequired

}

Players.defaultProps = {
    name: "Default Name",
    cost: "Def. Class",
    category: "F-"
}

export default Players